import React, { Component } from "react";
import Pdf from "react-to-pdf";

const ref = React.createRef();
class PDF extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hasil: [
        {
          nomor: "1",
          kode: 132,
          bruto: 32,
          persen: 5,
          netto: 30,
          pkg: 25,
          gross: 57,
          qty: 3,
          p: 1,
          k: 1,
          ongkos: 100000,
        },
        {
          nomor: "2",
          kode: 33,
          bruto: 322,
          persen: 52,
          netto: 302,
          pkg: 252,
          gross: 572,
          qty: 32,
          p: 12,
          k: 12,
          ongkos: 100000,
        },
        {
          nomor: "3",
          kode: 3333,
          bruto: 323,
          persen: 53,
          netto: 303,
          pkg: 253,
          gross: 537,
          qty: 33,
          p: 13,
          k: 13,
          ongkos: 100000,
        },
      ],
    };
  }
  render() {
    return (
      <>
        <div className="PDF" ref={ref}>
          <h3 className="text-center">{this.props.title}</h3>
          <table width="100%">
            <tr>
              <td>
                <td>
                  <p>No Terima</p>
                </td>
                <td>
                  <p>:</p>
                </td>
                <td>
                  <p>AS0192093019</p>
                </td>
              </td>
              <td>
                <td>
                  <p>Tanggal</p>
                </td>
                <td>
                  <p>:</p>
                </td>
                <td>
                  <p>AS0192093019</p>
                </td>
              </td>
            </tr>
            <tr>
              <td>
                <td>
                  <p>No Bon</p>
                </td>
                <td>
                  <p>:</p>
                </td>
                <td>
                  <p>AS0192093019</p>
                </td>
              </td>
              <td>
                <td>
                  <p>Supplier</p>
                </td>
                <td>
                  <p>:</p>
                </td>
                <td>
                  <p>AS0192093019</p>
                </td>
              </td>
            </tr>
          </table>
          <table width="100%">
            <thead>
              <tr>
                <td>No</td>
                <td>Kode</td>
                <td>Bruto(Gr)</td>
                <td>%</td>
                <td>Netto(Gr)</td>
                <td>Pkg(Gr)</td>
                <td>Gross(Gr)</td>
                <td>Qty</td>
                <td>P</td>
                <td>K</td>
                <td>Ongkos(Rp)</td>
              </tr>
            </thead>
            <tbody>
              {this.state.hasil.map((list) => (
                <tr>
                  <td>{list.nomor}</td>
                  <td>{list.kode}</td>
                  <td>{list.bruto}</td>
                  <td>{list.persen}</td>
                  <td>{list.netto}</td>
                  <td>{list.pkg}</td>
                  <td>{list.gross}</td>
                  <td>{list.qty}</td>
                  <td>{list.p}</td>
                  <td>{list.k}</td>
                  <td>{list.ongkos}</td>
                </tr>
              ))}
            </tbody>
          </table>
          <p>{this.props.content}</p>
        </div>
        <Pdf targetRef={ref} filename="post.pdf">
          {({ toPdf }) => <button onClick={toPdf}>Capture as PDF</button>}
        </Pdf>
      </>
    );
  }
}

export default PDF;
